<template lang="html">
  <div class="def-head">
    <div class="head-left" >
      <img v-if="hasArrow" @click="toGoPath" src="../../assets/img/head/callback_icon@3x.png" alt="">

    </div>
    <div class="head-cnt">{{header}}</div>
    <div class="head-right">
        <slot name="head-right"></slot>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    header: {
      type: String,
      default: ''
    },
    hasArrow: {
      type: Boolean,
      default: true
    },
    path: {
      type: String,
      default: ''
    },
    query: {
      type: Object,
      default () {
        return {}
      }
    }
  },
  methods: {
    toGoPath () {
      if (!this.path) {
        this.$router.go(-1)
      } else {
        this.$router.push({path: this.path, query: {...this.$route.query, ...this.query}})
      }
    }
  }
}
</script>

<style lang="less">
@import '../../assets/css/variable.less';
@import '../../assets/css/property.less';
.def-head {
  height: .88rem;
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  z-index: 5;
  background: @whiteColor;
  padding: 0.2rem;
  border-bottom: 1px solid @lineColor;
  .head-left {
    width: .6rem;
    text-align: left;
    float: left;
    img {
      width: .46rem;
      height: .46rem;
    }
  }
  .head-cnt{
    line-height: .48rem;
    font-size: .36rem;
    color: @defaultColor;
    margin: 0 .6rem;
    text-align: center;
  }
  .head-right {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: .2rem;;
    font-size: .3rem;
  }
}
</style>



