<template lang="html">
  <div class="foot-btn-box">
    <button class="foot-btn" type="button" name="button">{{text}}</button>
  </div>
</template>

<script>
export default {
  props: {
    text: {
      type: String,
      default: '提交'
    }
  }
}
</script>

<style lang="less">
@import '../../assets/css/variable.less';
@import '../../assets/css/property.less';
  .foot-btn-box {
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 999;
    .foot-btn {
      width: 100%;
      padding: .3rem 0;
      background: @greenBtn;
      text-align: center;
      color:@whiteColor;
      border: 1px solid @greenBorder;
      font-size: .32rem;
    }
  }
</style>



