<template lang="html">
  <div class="complaint-line">
    <form-item-line width="1.42rem" :text="leftText">
      <span v-if="rightText" class="right-text" :style="{color: rightColor}">{{rightText}}</span>
      <input v-else class="right-input" type="text" v-model="curValue" @input="$emit('input', curValue)"
       :placeholder="placeholder">
    </form-item-line>

  </div>
</template>

<script>
import formItemLine from '@/components/common/formItemLine'

export default {
  props: {
    leftText: {
      type: String,
      default: ''
    },
    rightText: {
      type: String,
      default: ''
    },
    rightColor: {
      type: String,
      default: ''
    },
    placeholder: {
      type: String,
      default: ''
    },
    value: {}
  },
  watch: {
    value (val) {
      this.curValue = val
    }
  },
  data () {
    return {
      curValue: this.value
    }
  },
  components: { formItemLine }
}
</script>

<style lang="less" scoped>
@import '../../assets/css/variable.less';

.complaint-line {
  padding: 0 .22rem;
  background: @whiteColor;
  .right-text {
    font-size: .3rem;
  }
  .right-input {
    height: .68rem;
    width: 100%;
    font-size: .28rem;
    border-radius: 0.05rem;
    border: 1px solid @lightColor;
    text-indent: .05rem;
    color: @midleColor
  }
}

</style>



