<template lang="html">
  <div class="form-item-line" :class="[formLineClass]" :style="{padding: padding, marginTop: marginTop, height: height, lineHeight: lineHeight}">
    <div class="form-item-line-left" :style="{'width': width, lineHeight: leftLineHeight}">
      {{text}}
    </div>
    <div class="form-item-line-right">
      <slot></slot>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    width: {
      type: String,
      default: '1.4rem'
    },
    height: {
      type: String,
      default: ''
    },
    lineHeight: {
      type: String,
      default: '.88rem'
    },
    leftLineHeight: {
      type: String,
      default: '.88rem'
    },
    formLineClass: {
      type: String,
      default: ''
    },
    text: {
      type: String,
      default: '搜索'
    },
    padding: {
      type: String,
      default: '0'
    },
    marginTop: {
      type: String,
      default: '0'
    }
  }
}
</script>

<style lang="less" scoped>
@import '../../assets/css/variable.less';
.form-item-line {
  color: @defaultColor;
  font-size: .3rem;
  overflow: hidden;
  .form-item-line-left {
    float: left;
  }
  .form-item-line-right {
    overflow: hidden;
    position: relative;
  }
}
</style>



