<template lang="html">
  <div class="complaint-form-page">
    <!-- 头部 -->
    <default-head header="申诉"></default-head>
    <!-- 表单 -->
    <form class="" action="#" method="post">
      <complaint-line leftText="投诉时间" :rightText="appealInfo.updateTime" rightColor="#999"></complaint-line>
      <complaint-des desColor="#999" leftText="投诉描述" :personText="personText" :imgList="appealInfo.picArray"
      :reasonText="appealInfo.content" desText="默认投诉描述"></complaint-des>

      <div class="form-bottom">
        <form-item-line text="描述" padding="0 .22rem" marginTop=".1rem" leftLineHeight=".68rem">
          <textarea class="textarea-des" placeholder="请输入您投诉的详情"
            v-model="applyInfo.content" data-vv-name="描述" v-validate="'required'" name="content"
          ></textarea>
        </form-item-line>
        <!-- 图片上传 -->
        <div class="img-upload clearfix">
          <div class="img-text fl">
            <p class="prove">图片证据</p>
            <p class="option">选填</p>
          </div>
          <div class="img-item fl" >
            <label for="pic_load">
              <img :src="applyInfo.picArray[0] || defaultimg" alt="">
              <input @change="changeFile($event, 0)" type="file" id="pic_load" style="display:none">
            </label>
          </div>
          <div class="img-item fl">
            <label for="pic_load2">
              <img :src="applyInfo.picArray[1] || defaultimg" alt="" alt="">
              <input @change="changeFile($event, 1)" type="file" id="pic_load2" style="display:none">
            </label>
          </div>
        </div>
        <foot-btn @click.native="submitAppealInfo"></foot-btn>
      </div>
    </form>
  </div>
</template>

<script>
import api from '@/api'
import utils from '@/utils/utils'
import minutils from '@/utils/minUtils'
import defaultHead from '@/components/head/defaultHead'
import complaintSelect from '@/components/complaintForm/complaintSelect'
import complaintLine from '@/components/complaintForm/complaintLine'
import complaintDes from '@/components/complaintForm/complaintDes'
import formItemLine from '@/components/common/formItemLine'
import footBtn from '@/components/common/footBtn'
import defaultimg from '@/assets/img/recognize/ph@3x.png'

export default {
  components: { defaultHead, complaintSelect, complaintLine, formItemLine, complaintDes, footBtn },
  data () {
    return {
      defaultimg,
      appealInfo: {
        complaintStatus: 0,
        content: '',
        id: 0,
        mobile: '',
        nickName: '',
        status: 0,
        title: '',
        updateTime: '',
        picArray: [],
        type: 0
      },
      applyInfo: {
        userId: utils.userId(),
        content: '',
        title: '申诉',
        picArray: []
      }
    }
  },
  computed: {
    personText () {
      return '用户' + (this.appealInfo.realName || '') + '（' + this.appealInfo.mobile + '）投诉'
    }
  },
  created () {
    this.initPage()
  },
  methods: {
    initPage () {
      let appealInfo = sessionStorage.getItem('appealform')
      if (appealInfo) {
        this.appealInfo = JSON.parse(appealInfo)
      } else {
        this.messageBox()
      }
    },
    messageBox () {
      let self = this
      minutils.MessageBox({
        message: '您未选择相关投诉内容',
        callback () { self.$router.push('becomplaintnotes') }
      })
    },
    submitAppealInfo () {
      let data = {
        userId: this.applyInfo.userId,
        relateId: this.appealInfo.id,
        type: this.appealInfo.type,
        content: this.applyInfo.content,
        picArray: this.applyInfo.picArray
      }
      this.ajaxSaveAppeal(data)
    },
    changeFile (e, index) {
      utils.uploadPic(e.target.files, (data) => {
        this.ajaxUploadPic({fileName: data[0].fileName, fileBase64: data[0].base64Img}, index)
      })
    },
    ajaxUploadPic (data, index) {
      api.uploadPic(data).then(response => {
        let result = response.body
        if (result.isSuccess) {
          this.applyInfo.picArray[index] = result.data
          this.applyInfo.picArray = this.applyInfo.picArray.concat()
        }
      })
    },
    ajaxSaveAppeal (data) {
      api.saveAppeal(data).then(response => {
        let result = response.body
        let self = this
        if (result.isSuccess && result.data) {
          minutils.MessageBox({
            message: '提交申诉信息成功',
            callback () {
              self.$router.push('/becomplaintnotes')
            }
          })
        }
      })
    }
  }
}
</script>

<style lang="less" scoped>
@import '../assets/css/variable.less';

.complaint-form-page {
  padding-top: .88rem;
  height: 100%;
  .textarea-des {
    width: 5.66rem;
    height: 3.4rem;
    font-size: .28rem;
    color: @midleColor;
    padding: .1rem .1rem .1rem;
    border-radius: .05rem;
  }
  .form-bottom {
    background: @whiteColor;
    margin-top: .3rem;
    padding-top: .2rem;
    padding-bottom: 2rem;
  }
  .img-upload {
    padding: 0 .22rem;
    .img-text {
      width: 1.42rem;
      font-size: .3rem;
      .option {
        color: @lightColor;
        margin: .2rem 0 0 .3rem;
      }
    }
    .img-item {
      img {
        width: 2rem;
        height: 1.2rem;
      }
      &:nth-child(3) {
        margin-left: .4rem;
      }
    }
  }

}

</style>



