<template lang="html">
  <div class="complaint-des">
    <form-item-line width="1.42rem" :text="leftText" height="3.3rem">
      <p class="des-text">{{personText}}</p>
      <p class="des-text">{{reasonText}}</p>
      <p class="des-text" :style="{color: desColor}">{{desText}}</p>
      <div class="img-box">
        <img v-for="item in imgList" :src="item || defaultimg" alt="">
        <!-- <img src="../../assets/img/recognize/ph@3x.png" alt=""> -->
      </div>
    </form-item-line>

  </div>
</template>

<script>
import formItemLine from '@/components/common/formItemLine'
import defaultimg from '@/assets/img/recognize/ph@3x.png'
export default {
  props: {
    personText: {
      type: String,
      default: ''
    },
    reasonText: {
      type: String,
      default: ''
    },
    desText: {
      type: String,
      default: ''
    },
    leftText: {
      type: String,
      default: ''
    },
    desColor: {
      type: String,
      default: '#666'
    },
    imgList: {
      type: Array,
      default () {
        return new Array(2)
      }
    }
  },
  data () {
    return {
      defaultimg
    }
  },
  components: { formItemLine }
}
</script>

<style lang="less" scoped>
@import '../../assets/css/variable.less';

.complaint-des {
  padding: 0 .22rem;
  background: @whiteColor;
  .des-text {
    font-size: .3rem;
    height: .3rem;
    margin-bottom: .26rem;
    // line-height: .3rem;
  }
  .img-box {
    img {
      width: 2rem;
      height: 1.2rem;
      border-radius: .05rem;
      margin-top: .3rem;
      &:last-child {
        margin-left: .3rem;
      }
    }
  }
}

</style>



