<template lang="html">
  <div class="complaint-select">
    <form-item-line width="1.42rem" :text="text">
      <select class="js-select1" @change="$emit('change', curValue)" v-model="curValue">
        <option value="" style="display:none">{{defOption}}</option>
        <option v-for="item in selectList" :value="item.value" >{{item.name}}</option>
      </select>
    </form-item-line>

  </div>
</template>

<script>
import formItemLine from '@/components/common/formItemLine'

export default {
  components: { formItemLine },
  props: {
    defOption: {
      type: String,
      default: ''
    },
    text: {
      type: String,
      default: '投诉主体'
    },
    selectList: Array,
    value: {}
  },
  watch: {
    value (val) {
      this.curValue = val
    },
    curValue (val) {
      this.$emit('input', val)
    }
  },
  data () {
    return {
      curValue: this.value
    }
  }
}
</script>

<style lang="less" scoped>
@import '../../assets/css/variable.less';

.complaint-select {
  padding: 0 .22rem;
  background: @whiteColor;
  select {
    width: 5.64rem;
    height: .68rem;
    padding-left: .1rem;
    font-size: .28rem;
    border-radius: .05rem;
    color: @midleColor;
    border: 1px solid @lightColor;
    appearance: none;
    -moz-appearance: none;
    -webkit-appearance: none;
    -ms-appearance: none;
    background: url(../../assets/img/appealSelect/select@2x.png) no-repeat right center;
    background-size: .44rem .44rem;
  }
}

</style>



