import Vue from 'vue'
import VueResource from 'vue-resource'
import md5 from 'md5'
import utf8 from 'utf8'
import base64 from 'base-64'
import minutils from '@/utils/minUtils'

Vue.use(VueResource)
// http相关
Vue.http.options.emulateJSON = true
Vue.http.options.xhr = { withCredentials: true }

const appId = '1493037951465'
const appSecret = 'aa5009503ead3a60180779f1d1db3227'

// 接口预处理
Vue.http.interceptors.push(function (request, next) {
  // 接收参数判定是否需要验证isSuccess
  var req = {}
  var ifSuccess = request.body ? request.body.ifSuccess ? request.body.ifSuccess : false : false
  req[request.params.action] = ifSuccess

  // 请求时对请求进行加密
  try {
    console.log(request.body)
    let json = utf8.encode(JSON.stringify({ tokenUserId: 1, token: md5(appId + appSecret), ...request.body }))
    var data = base64.encode(json)
  } catch (e) {
    console.log(e)
  }
  request.body = { jsonString: data }
  // 对返回接口结果预处理 (如：判断是否已登录)
  next(function (response) {
    if (response.body.code !== 200) {
      minutils.Toast({ message: '网络异常, 操作失败', position: 'top' })
      return
    }

    let url = response.url
    let action = url.substring(url.indexOf('=') + 1, url.length)
    if (!response.body.isSuccess && !req[action]) {
      minutils.Toast({ message: response.body.description, position: 'top' })
      return
    }
  })
})

// http://120.76.232.171:8087/
// const API_ROOT = (process.env.NODE_ENV === 'production') ? 'http://hzl-api.jufeng.co/api' : 'http://120.76.232.171:8090/api'
// const API_ROOT = (process.env.NODE_ENV === 'production') ? 'http://hzl-api.jufeng.co/api' : 'http://hzl-api.jufeng.co/api'
const API_ROOT = 'http://120.76.232.171:8090/api'
// const API_ROOT = 'http://192.168.4.7:8090/api'
// linsheng
// const API_ROOT = 'http://192.168.4.21:8090/api'
// const API_ROOT = 'http://192.168.4.7:8090/api'
// const API_ROOT = (process.env.NODE_ENV === 'production') ? 'http://localhost:8090/api' : 'http://120.76.232.171:8090/api'
export const resource = Vue.resource(API_ROOT + '?action={action}')



